import { getTestOverrides } from '@wix/cli-test-overrides';
import pRetry from 'p-retry';
import type { Options } from 'p-retry';
import { isHttpError, isHttpApplicationError } from '@wix/cli-http-client';
import type { HttpClient, HttpError } from '@wix/cli-http-client';
import { CliError, CliErrorCode } from '@wix/cli-error';
import { queryPublishedContent } from '@wix/ambassador-multilingual-localization-public-v3-published-content/http';
import type {
  QueryPublishedContentRequest,
  QueryPublishedContentResponse,
  PublishedContent,
  Sorting,
} from '@wix/ambassador-multilingual-localization-public-v3-published-content/types';
import {
  SchemaScope,
  SortOrder,
} from '@wix/ambassador-multilingual-localization-public-v3-published-content/types';
import { queryLocales } from '@wix/ambassador-multilingual-locales-v2-locale/http';
import type {
  QueryLocalesRequest,
  QueryLocalesResponse,
} from '@wix/ambassador-multilingual-locales-v2-locale/types';

export type { PublishedContent } from '@wix/ambassador-multilingual-localization-public-v3-published-content/types';
export {
  SchemaScope,
  SortOrder,
} from '@wix/ambassador-multilingual-localization-public-v3-published-content/types';

const HEADLESS_APP_ID = '78640cbb-be47-45f8-b6de-8ed97287872c';
const HEADLESS_TRANSLATIONS_ENTITY_TYPE = 'headless-site';

export class MultilingualClient {
  private readonly retryOptions: Options;

  constructor(private readonly httpClient: HttpClient) {
    const { minRetryTimeout } = getTestOverrides();

    this.retryOptions = {
      retries: 3,
      minTimeout: minRetryTimeout,
      maxTimeout: 3 * 1000,
    };
  }

  queryPublishedContents = async (
    request: QueryPublishedContentRequest
  ): Promise<QueryPublishedContentResponse> => {
    try {
      const { data } = await pRetry(
        () =>
          this.httpClient.request(
            queryPublishedContent({
              ...request,
            })
          ),
        this.retryOptions
      );

      return data;
    } catch (error) {
      if (isHttpError(error) && isHttpApplicationError(error)) {
        this.handleApplicationError(error);
      }

      throw error;
    }
  };

  queryAllPublishedContents = async (
    request: QueryPublishedContentRequest = { query: {} }
  ): Promise<PublishedContent[]> => {
    const allContents: PublishedContent[] = [];
    let cursor: string | undefined;
    const limit = 100; // Maximum 100 items per request according to API docs
    let hasMore = true;

    while (hasMore) {
      let sort: Sorting[] | undefined = request.query.sort ?? [
        {
          fieldName: 'id',
          order: SortOrder.ASC,
        },
      ];
      let filter: Record<string, unknown> | null | undefined = {
        ...(request.query.filter ?? {}),
        'schemaKey.appId': HEADLESS_APP_ID,
        'schemaKey.scope': SchemaScope.GLOBAL,
        'schemaKey.entityType': HEADLESS_TRANSLATIONS_ENTITY_TYPE,
      };

      if (cursor) {
        sort = undefined;
        filter = undefined;
      }

      const response = await this.queryPublishedContents({
        query: {
          filter,
          cursorPaging: {
            limit,
            ...(cursor ? { cursor } : {}),
          },
          sort,
        },
      });

      const contents = response.publishedContent ?? [];
      allContents.push(...contents);

      const pagingMetadata = response.pagingMetadata;
      hasMore = pagingMetadata?.hasNext ?? false;
      cursor = pagingMetadata?.cursors?.next ?? undefined;

      if (hasMore && !cursor) {
        hasMore = false;
      }
    }

    return allContents;
  };

  queryLocales = async (
    request?: QueryLocalesRequest
  ): Promise<QueryLocalesResponse> => {
    try {
      const { data } = await pRetry(
        () =>
          this.httpClient.request(
            queryLocales({
              ...request,
            })
          ),
        this.retryOptions
      );

      return data;
    } catch (error) {
      if (isHttpError(error) && isHttpApplicationError(error)) {
        throw new CliError({
          code: CliErrorCode.FailedToQueryLocales(),
          cause: error,
        });
      }

      throw error;
    }
  };

  getPrimaryLanguage = async (): Promise<string | undefined> => {
    const response = await this.queryLocales();
    const primaryLocale = response.locales?.find(
      (locale) => locale.visitorPrimaryLocale === true
    );

    return primaryLocale?.languageCode ?? undefined;
  };

  private handleApplicationError(error: HttpError) {
    const applicationError = error.response?.data.details.applicationError;

    if (
      applicationError?.code === 'APP_NOT_INSTALLED' &&
      error.response?.status === 428
    ) {
      throw new CliError({
        code: CliErrorCode.MultilingualAppNotInstalled(),
        cause: error,
      });
    }

    throw new CliError({
      code: CliErrorCode.FailedToQueryPublishedContents(),
      cause: error,
    });
  }
}
