import React from 'react';
import type { FC } from 'react';
import { match } from 'variant';
import { Alert, Spinner, useAsync } from '@wix/cli-ui-kit';
import { useMultilingualClient } from '@wix/multilingual-client';
import type { PublishedContent } from '@wix/multilingual-client';
import { GIT_IGNORED_DIR } from '@wix/cli-astro-definitions';
import { join } from 'node:path';
import { useProjectModel } from '../../model.js';
import { saveTranslationFiles } from './save-translation-files.js';

export type TranslationPublishedContent = PublishedContent;

export const TranslationPull: FC<{ onComplete?: () => void }> = ({
  onComplete,
}) => {
  const { model } = useProjectModel();
  const multilingualClient = useMultilingualClient();

  const { status } = useAsync(async () => {
    const [allContents, primaryLanguage] = await Promise.all([
      multilingualClient.queryAllPublishedContents(),
      multilingualClient.getPrimaryLanguage(),
    ]);

    const multilingualDir = join(
      model.projectFolder,
      GIT_IGNORED_DIR,
      'multilingual'
    );
    const translationsDir = join(multilingualDir, 'translations');

    await saveTranslationFiles(
      translationsDir,
      multilingualDir,
      allContents,
      primaryLanguage
    );

    onComplete?.();
  }, []);

  return match(status, {
    Loading: () => <Spinner text="Pulling translation files..." />,
    Success: () => {
      return (
        <Alert type="success">
          Translation files written to "
          {join(GIT_IGNORED_DIR, 'multilingual/translations')}"
        </Alert>
      );
    },
    Error: () => null,
    NotRequested: () => null,
  });
};
